<template>
	<div class="home container">
		<div class="columns">
			<div class="column has-text-centered">
				<div class="fixed animate__delay-2s animate__animated animate__fadeInDown">
					<h2 class="title is-2">Description <span class="gen">Grabber</span></h2>

					<input class="input is-rounded" type="text" name="searchQuery" v-model="state.searchQuery" />

					<div class="select">
						<select v-model="state.num">
							<option value="0" selected>Result count?</option>
							<option v-for="(num, i) in 100" :key="i" :value="i+1">{{ i+1 }}</option>
						</select>
					</div>

					<div class="buttons is-centered">
						<button :disabled="state.num == 0" class="button is-primary is-rounded is-large" @click="search()">Grab</button>
					</div>
					
				</div>
			</div>	
			<div class="column is-6 list-wrapper">
					<div v-show="state.isLoading" class="loader-wrapper">
						<div class="fancy-spinner">
							<div class="ring"></div>
							<div class="ring"></div>
							<div class="dot"></div>
						</div>
					</div>

					<div 
						class="card animate__animated"  
						v-for="(result, i) in filtered" :key="i"
					>

						<div class="card-content">

							<div class="media">
								<!-- <div class="media-left">
									<figure class="image is-48x48">
										<img src="thumbnail" alt="Placeholder image">
									</figure>
								</div> -->
								<div class="media-content">
									<p class="subtitle is-5 person-name">{{ result.description }}
									</p>

									<a target="_blank" :href="result.link">Browse</a>
									
								</div>
							</div>

						</div>
						
					</div>
			</div>	
		</div>

	</div>
</template>

<script>
import { reactive, computed } from 'vue';

export default {
	name: 'Home',
	components: {
	},

	setup() {
		let state = reactive({
			searchResults: [],
			searchQuery: null,
			num: 0,
			isLoading: false,
			hasError: false,
		});

		const filtered = computed( () => {
			let data = state.searchResults;

			const newdata = data.filter(item => item.description != '');

			return newdata;
		});

		function search() {
			const url = `http://localhost:3000/search/?searchquery=${state.searchQuery}&num=${state.num}`;
			state.isLoading = true;

			return fetch(url)
					.then(response => response.json())
					.then(data => {
						state.searchResults = data;
						state.isLoading = false;
					});
		}

		return {
			state,
			search,
			filtered,
		}
	}
}
</script>

